/*CSS reset*/
*{box-sizing: border-box;}
*:before,*:after{box-sizing: border-box;}
*{margin: 0;padding: 0;}
ul,ol{list-style: none;}
a{color: inherit; text-decoration: none;}
img{max-width: 100%;max-height: 100%;}


/*style*/

body{background: #eee;}   /*浏览器会智能地把背景色填充到整个网页，即使body的区域只占一部分，可用border看*/

.globalHeader{
    margin: 20px;
    
}
@media (min-width: 500px){
    .globalHeader{
        margin: 60px 0 100px;
    }
}
.searchForm{
    display: flex;
    justify-content: space-between;
}
@media (min-width:500px){
    .searchForm{
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
    }
}
.searchForm > input{
    width: 100%;
    height: 40px;
    margin-right: 15px;
    padding: 0 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.searchForm > button{
    white-space: nowrap;
    padding: 0 24px;
    border-radius: 4px;
    border: none;
    background: rgba(11, 27, 172, 0.76);
    color: white;
    font-size: 16px;
}

.globalMain{
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}

.siteList{
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
@media (min-width: 500px) {
    .siteList{
        margin-right: -25px;
        margin-left: 0;
        justify-content: flex-start;
    }
}
.siteList > li{
    margin-bottom: 10px;
}
@media(min-width:500px){
    .siteList > li{
        margin-right: 25px;

    }
}
.siteList .site{
    width: 160px;
    height: 120px;
    background: #FFFFFF;
    border: 1px solid #DDDDDD;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 4px;
    padding: 20px 0;
    position: relative;
    cursor: pointer;
}
.siteList .site > .logo{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 64px;
}
.siteList .site > .link{
    font-size: 14px;
    margin-top: 4px;
}
.siteList .site > .close{
    position: absolute;
    right: 5px;
    top: 5px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;
    cursor: default;
}
.siteList .site:hover > .close{
    display: block;
}
.siteList .addButton{
    border: 1px solid #ddd;
    border-radius: 4px;
    background: white;
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 15px 0;
}
.siteList .addButton .iconWrapper{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.siteList .addButton .icon{
    width: 64px;
    height: 64px;

}
.siteList .addButton .text{
    font-size: 14px;
    margin-top: 4px;
}